import { v4 as uuidv4 } from "uuid";
import { connect } from "react-redux";
import { useState } from "react";
import "./css/ModalItems.css";

const ModalItems = ({
	items,
	categories,
	addNewItem,
	updateItem,
	removeItem,
	displayModal,
	setFilterMenu,
	filterMenu,
	manageQty,
}) => {
	const [id, setId] = useState(uuidv4());
	const [name, setName] = useState("");
	const [price, setPrice] = useState(0);
	const [category, setCategory] = useState("");
	const [newCategory, setNewCategory] = useState("");
	const [image, setImage] = useState("");
	const [displayInputNewCategory, setDisplayInputNewCategory] = useState(false);
	const [updatePrevCategory, setUpdatePrevCategory] = useState("");
	const [search, setSearch] = useState("");
	const [isUpdate, setIsUpdate] = useState("");
	const [existName, setExistName] = useState(false);
	const [existCategory, setExistCategory] = useState(false);
	const [empty, setEmpty] = useState(false);

	const setInput = (item) => {
		setId(item.id);
		setName(item.name);
		setCategory(item.category);
		setUpdatePrevCategory(item.category);
		setPrice(item.price);
		setImage(item.image);
		setIsUpdate(item.name);
		setDisplayInputNewCategory(false);
	};

	const nameChangeHandler = (e) => {
		setName(e.target.value);
		setExistName(false);
		setEmpty(false);
	};

	const priceChangeHandler = (e) => {
		setPrice(e.target.value);
		setEmpty(false);
	};

	const imageChangeHandler = (e) => {
		setImage(e.target.value);
		setEmpty(false);
	};

	const selectCategoryChangeHandler = (e) => {
		if (e.target.value === "add-new-category") {
			setDisplayInputNewCategory(true);
		} else {
			setDisplayInputNewCategory(false);
			setNewCategory("");
		}
		setCategory(e.target.value);
		setExistCategory(false);
		setEmpty(false);
	};

	const newCategoryChangeHandler = (e) => {
		setNewCategory(e.target.value);
		setExistCategory(false);
		setEmpty(false);
	};

	const clearInput = () => {
		if (isUpdate) {
			setId(uuidv4());
		}
		setName("");
		setPrice(0);
		setCategory("");
		setUpdatePrevCategory("");
		setNewCategory("");
		setImage("");
		setIsUpdate("");
		setDisplayInputNewCategory(false);
	};

	//set filtermenu to all if items in filter menu becomes empty
	const setFilterMenuToAll = (action, category) => {
		//check if item category was changed
		if (filterMenu === updatePrevCategory) {
			if (action === "remove") {
				if (
					items.filter(
						(item) => item.category === category && item.status === "active"
					).length === 0
				) {
					setFilterMenu("all");
				}
			} else if (action === "update") {
				if (filterMenu === updatePrevCategory) {
					if (
						items.filter(
							(item) =>
								item.category === updatePrevCategory && item.status === "active"
						).length === 1
					) {
						setFilterMenu("all");
					}
				}
			}
		}
	};

	const manageItemClickHandler = (action) => {
		let exists = false;
		let empty = false;

		if (name === "" || price === "" || image === "" || category === "") {
			empty = true;
			setEmpty(true);
		}

		items.forEach((item) => {
			if (item.name.toLowerCase() === name.toLowerCase().trim()) {
				exists = true;
				setExistName(true);
				if (
					isUpdate &&
					item.name.toLowerCase() === isUpdate.toLowerCase().trim()
				) {
					exists = false;
					setExistName(false);
				}
			}
		});

		if (category === "add-new-category") {
			if (newCategory === "") {
				empty = true;
				setEmpty(true);
			} else {
				categories.forEach((category) => {
					if (category.toLowerCase() === newCategory.toLowerCase().trim()) {
						exists = true;
						setExistCategory(true);
					}
				});
			}
		}

		if (exists === false && empty === false) {
			let inputCategory = "";
			let item = {};

			if (category === "add-new-category") {
				inputCategory = newCategory.trim();
			} else {
				inputCategory = category;
			}

			item = {
				id: id,
				name: name.trim(),
				price: price,
				category: inputCategory.trim(),
				image: image.trim(),
				status: "active",
			};

			if (action === "add") {
				addNewItem(item);
				alert("Item added!");
			} else if (action === "update") {
				let index = items.findIndex((item) => item.name === isUpdate);
				if (
					name === items[index].name &&
					price === items[index].price &&
					category === items[index].category &&
					image === items[index].image
				) {
					alert("No changes.");
				} else {
					updateItem(item);
					alert("Item updated!");
					setFilterMenuToAll("update", inputCategory);
				}
			} else if (action === "remove") {
				removeItem(item.id);
				alert("Item removed!");
				clearInput();
				setFilterMenuToAll("remove", category);
				manageQty(item.id, "cancel");
			}
			setId(uuidv4());
			clearInput();
		}
	};

	const inputSearchChangeHandler = (e) => {
		setSearch(e.target.value);
	};

	return (
		<div id="div-modal-items">
			<button
				id="btn-close"
				onClick={() => {
					displayModal(false);
				}}
			>
				&times;
			</button>

			<div id="div-modal-form">
				<h2>Items</h2>

				{isUpdate ? <p id="p-update-item">Update Item: {isUpdate}</p> : null}

				<label for="input-item-id">Id: </label>
				<input type="text" id="input-item-id" value={id} disabled />
				<label for="input-item-name">Name: </label>
				<input
					type="text"
					id="input-item-name"
					onChange={nameChangeHandler}
					value={name}
				/>
				{existName ? (
					<p>
						<small>Name already exist!</small>
					</p>
				) : null}

				<label for="input-item-price">Price: </label>
				<input
					type="number"
					id="input-item-price"
					onChange={priceChangeHandler}
					value={price}
				/>
				<label for="select-item-category">Category: </label>
				<select
					id="select-item-category"
					value={category}
					onChange={selectCategoryChangeHandler}
				>
					<option value="">select category</option>
					{categories.map((category) => (
						<option value={category}>{category}</option>
					))}
					<option value="add-new-category">add new category</option>
				</select>
				{displayInputNewCategory === true ? (
					<input
						type="text"
						id="input-new-category"
						onChange={newCategoryChangeHandler}
						value={newCategory}
					/>
				) : null}
				{existCategory ? (
					<p>
						<small>Category already exist!</small>
					</p>
				) : null}

				<label for="input-item-image">Image: </label>
				<input
					type="url"
					id="input-item-image"
					onChange={imageChangeHandler}
					value={image}
				/>

				{empty ? (
					<p>
						<small>All fields must be filled out!</small>
					</p>
				) : null}

				<p id="p-modal-buttons">
					{!isUpdate ? (
						<button id="btn-add" onClick={() => manageItemClickHandler("add")}>
							Add
						</button>
					) : null}

					{isUpdate ? (
						<button
							id="btn-update"
							onClick={() => manageItemClickHandler("update")}
						>
							Update
						</button>
					) : null}

					{isUpdate ? (
						<button
							id="btn-remove"
							onClick={() => manageItemClickHandler("remove")}
						>
							Remove
						</button>
					) : null}

					<button id="btn-clear" onClick={clearInput}>
						Cancel
					</button>
				</p>
			</div>

			<div id="div-modal-search-item">
				<label for="input-search">Search item: </label>
				<input
					type="search"
					id="input-search"
					placeholder="id,name,category"
					onChange={inputSearchChangeHandler}
				/>

				<div>
					<table id="table-search">
						<thead>
							<th>Id</th>
							<th>Name</th>
							<th>Price</th>
							<th>Category</th>
						</thead>
						<tbody>
							{search === ""
								? items
										.filter((item) => item.status === "active")
										.map((filteredItem) => (
											<tr onClick={() => setInput(filteredItem)}>
												<td>{filteredItem.id}</td>
												<td>{filteredItem.name}</td>
												<td>{filteredItem.price}</td>
												<td>{filteredItem.category}</td>
											</tr>
										))
								: items
										.filter(
											(item) =>
												(item.id
													.trim()
													.toLowerCase()
													.includes(search.trim().toLowerCase()) ||
													item.name
														.trim()
														.toLowerCase()
														.includes(search.trim().toLowerCase()) ||
													item.category
														.trim()
														.toLowerCase()
														.includes(search.trim().toLowerCase())) &&
												item.status === "active"
										)
										.map((filteredItem) => (
											<tr onClick={() => setInput(filteredItem)}>
												<td>{filteredItem.id}</td>
												<td>{filteredItem.name}</td>
												<td>{filteredItem.price}</td>
												<td>{filteredItem.category}</td>
											</tr>
										))}
						</tbody>
					</table>
				</div>
			</div>
		</div>
	);
};

const mapStateToProps = ({ items, categories, filterMenu }) => {
	return {
		items,
		categories,
		filterMenu,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		addNewItem: (newItem) => dispatch({ type: "addNewItem", newItem: newItem }),
		updateItem: (item) => dispatch({ type: "updateItem", item: item }),
		removeItem: (id) => dispatch({ type: "removeItem", id: id }),
		setFilterMenu: (category) =>
			dispatch({ type: "setFilterMenu", filterCategory: category }),
		manageQty: (id, operation) =>
			dispatch({ type: "manageQty", id: id, operation: operation }),
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(ModalItems);
