import "./css/Navigation.css";
import { useEffect } from "react";
import { connect } from "react-redux";

const Navigation = ({
	categories,
	setFilterMenu,
	filterMenu,
	items,
	setCategories,
}) => {
	const activeCategory = () => {
		const btnCategory = document.querySelectorAll("#div-navigation > button");
		btnCategory.forEach((category) => {
			if (category.textContent === filterMenu) {
				category.classList.add("active");
			} else {
				category.classList.remove("active");
			}
		});
	};

	const btnCategoryClickHandler = (e) => {
		setFilterMenu(e.target.textContent);
	};

	const listCategories = () => {
		let categories = [];
		items.forEach((item) => {
			if (item.status === "active") {
				if (categories.includes(item.category) === false) {
					categories.push(item.category);
				}
			}
		});
		setCategories(categories);
	};

	useEffect(() => {
		activeCategory();
	});

	useEffect(() => {
		listCategories();
		activeCategory();
	}, [items]);

	return (
		<nav id="div-navigation">
			<button className="active" onClick={btnCategoryClickHandler}>
				all
			</button>
			{categories.sort().map((category) => (
				<button className="" onClick={btnCategoryClickHandler}>
					{category}
				</button>
			))}
		</nav>
	);
};

const mapStateToProps = ({ categories, filterMenu, items }) => {
	return {
		categories,
		filterMenu,
		items,
	};
};

const mapDispatchToProps = (dispatch) => {
	return {
		setFilterMenu: (category) =>
			dispatch({ type: "setFilterMenu", filterCategory: category }),
		setCategories: (categories) =>
			dispatch({ type: "setCategories", categories: categories }),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(Navigation);
