import React from 'react';
import Header from './components/Header';
import Body from './components/Body';
import ModalItems from './components/ModalItems';
import './App.css';

class App extends React.Component{
  state= {
    displayModal:false,
  }

  displayModal = (action) => {
    if(action===true){
      this.setState({displayModal:true,})
    }else
    if(action===false){
      this.setState({displayModal:false,})
    }
  }

  render(){
    return(
      <div id="div-app">
          {this.state.displayModal===true ? <ModalItems displayModal={this.displayModal} /> : null}
        <div id="div-app-container">
          <Header displayModal={this.displayModal} />
          <Body />
        </div>
      </div>
    );
  }
}

export default App;