import { connect } from "react-redux";
import "./css/CartItem.css";

const CartItem = ({ item, items, cart, manageQty }) => {
	const indexItems = items.findIndex((indexItem) => indexItem.id === item.id);
	const indexCart = cart.findIndex(
		(indexCartItem) => indexCartItem.id === item.id
	);
	return (
		<div className="div-cart-item">
			<p>
				<strong>{items[indexItems].name}</strong>
			</p>
			<div>
				<p>
					Qty
					<p className="p-manage-qty">
						{cart[indexCart].qty > 1 ? (
							<button
								className="btn-manage-qty"
								onClick={() => manageQty(item.id, "decrement")}
							>
								{" "}
								-{" "}
							</button>
						) : null}
						<span> {cart[indexCart].qty} </span>
						<button
							className="btn-manage-qty"
							onClick={() => manageQty(item.id, "increment")}
						>
							{" "}
							+{" "}
						</button>
					</p>
				</p>
			</div>
			<p>
				<small>
					Subtotal:
					<br /> Php {cart[indexCart].qty * cart[indexCart].price}
				</small>
			</p>
			<p>
				<button
					className="btn-cancel"
					onClick={() => manageQty(item.id, "cancel")}
				>
					Cancel
				</button>
			</p>
		</div>
	);
};

const mapDispatchToProps = (dispatch) => {
	return {
		manageQty: (id, operation) =>
			dispatch({ type: "manageQty", id: id, operation: operation }),
	};
};

const mapStateToProps = ({ items, cart }) => {
	return {
		items,
		cart,
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(CartItem);
