import { connect } from 'react-redux';
import MenuItem from './MenuItem';
import Navigation from './Navigation';
import './css/Menu.css';

const Menu = ({items,filterMenu}) => {
    const sortByName = (a, b) => {
        let nameA = a.name.toLowerCase(),
            nameB = b.name.toLowerCase();
    
        if (nameA < nameB) {
            return -1;
        }
        if (nameA > nameB) {
            return 1;
        }
        return 0;
    }
    return(
        <section id="div-menu">
            <h2>Menu</h2>
            <Navigation />
            <div id="div-menu-items-container">
            {items.length === 0 ? <p>Menu is empty</p> :
                filterMenu === "all" ? 
                items.filter(itemAll => itemAll.status === "active").sort(sortByName).map(
                filterItemAll => <MenuItem item={filterItemAll}/>) : 
                items.filter(item => item.category === filterMenu && 
                item.status === "active").sort(sortByName).map(filterItem => 
                <MenuItem item={filterItem}/>)}
            </div>
        </section>
    );
}

const mapStateToProps = ({items,filterMenu}) => {
    return {
        items,
        filterMenu,
    }
}
export default connect(mapStateToProps)(Menu);